import { useContext } from 'react'
import AllCompo from '../components/AllCompo'
import Footer from '../components/Footer'
import { useParams } from 'react-router'
import {apiValue} from "../Data/AllData"
import {Link} from 'react-router-dom';
import { CartProvider } from 'react-use-cart'



function AllSingleProduct() {
    const{id} = useParams();
    const data = useContext(apiValue);
  return (
    <CartProvider>
        <AllCompo/>
        <div className='container mt-3 pt-5'>
        {data.map((item)=>{
         return item.id == id?(
            <div className='row'>
              <div className='col-md-6 text-center'>
                <img src={item.images[0]} className='w-75'></img>
              </div>
           <div className='col-md-6 text-center mt-4 text-dark'>
  <h5>Name: {item.title}</h5>
  <h5 className='mt-4 text-dark'>Brand: {item.brand}</h5>
  <h5 className='mt-4 text-dark'>Category: {item.category}</h5>
  <h5 className='mt-4 text-dark'>Stock: {item.stock}</h5>
  <h5 className='mt-4 text-dark'>Rating: {item.rating} of 5</h5>
  <h5 className='mt-4 text-dark'>Discount: {item.discountPercentage}%</h5>
  <h5 className='mt-4 text-dark'>Price: {item.price}$</h5>
  <h5 className='mt-4 text-dark'>ID: {item.id}</h5>

  <Link to="/products" className='btn btn-dark d-block w-100 mt-3'>Back</Link>
</div>

            </div>
         ):null

         
        })}
        </div>
        <Footer/>
    </CartProvider>
  )
}

export default AllSingleProduct