import React from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

function Banner() {
  return (
    <div>
        <div className="main-banner">
  <div className="container">
    <div className="row">
      <div className="col-lg-6 align-self-center">
        <div className="header-text">
          <h6>Liberty NFT Market</h6>
          <h2>Create, Sell &amp; Collect Top NFTs.</h2>
          <p>
            Liberty NFT Market is a really cool and professional design for your
            NFT websites. This HTML CSS template is based on Bootstrap v5 and it
            is designed for NFT related web portals. Liberty can be freely
            downloaded from TemplateMo's free css templates.
          </p>
          <div className="buttons">
            <div className="border-button">
              <a href="explore.html">Explore Top NFTs</a>
            </div>
            <div className="main-button">
              <a href="https://youtube.com/templatemo" target="_blank">
                Watch Our Videos
              </a>
            </div>
          </div>
        </div>
      </div>
      <div className="col-lg-5 offset-lg-1">
        <owl-carousel className="owl-banner" >
          <div className="item">
            <img src="assets/images/banner-01.png" alt="" />
          </div>
          <div className="item">
           
          </div>
        </owl-carousel>
      </div>
    </div>
  </div>
</div>

    </div>
  )
}

export default Banner