import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import { createBrowserRouter, RouterProvider,} from "react-router-dom";
import { AllData } from './Data/AllData';
import ScrollToTop from 'react-scroll-to-top';
import AllExplore from './Explore/AllExplore';
import AllDetails from './Details/AllDetails';
import AllAuthor from './author/AllAuthor';
import AllCreate from './create yours/AllCreate';
import AllProducts from './products/AllProducts';
import AllSingleProduct from './SingleProduct/AllSingleProduct';
import AllCart from './Cart/AllCart';

let router = createBrowserRouter([
  {
    path: "/",
    element: <App/>
  },
  {
    path: "explore",
    element: <AllExplore/>
  },
  {
    path: "details",
    element: <AllDetails/>
  },
  {
    path: "author",
    element: <AllAuthor/>
  },
  {
    path: "create",
    element: <AllCreate/>
  },
  {
    path: "products",
    element: <AllProducts/>
  },
  {
    path: "singleProduct/:id",
    element: <AllSingleProduct/>
  },
  {
    path: "cart",
    element: <AllCart/>
  },
 

]);

ReactDOM.createRoot(document.getElementById('root')).render(
  <AllData>
    <>
    <RouterProvider router={router} />,
    <ScrollToTop smooth/>
    </>
  </AllData>
);






